<template>
  <div class="content">
    <el-tabs type="card">
      <el-tab-pane>
        <span slot="label"><i class="el-icon-date"></i> 会议日程</span>

        <div class="meeting-category" v-if="todayMeeting.length">
          <span>今天</span>
          <el-card v-for="item in todayMeeting" :key="item.id" class="h-300">
            <el-row style="text-align: center"
              ><strong :title="item.title">{{ item.title }}</strong></el-row
            >
            <el-row
              ><span class="meeting-time-row"
                ><i class="el-icon-date"></i>{{ item.time }}</span
              ><span class="meeting-place-row" :title="item.place"
                ><i class="el-icon-location"></i>{{ item.place }}</span
              ></el-row
            >
            <el-row style="margin: 5px 0 10px 0"
              >参会人员：<span :title="item.attendee">{{
                item.attendee
              }}</span></el-row
            >
            <div class="meeting-content">会议内容：{{ item.content }}</div>
          </el-card>
        </div>
        <div class="meeting-category" v-if="notTodayMeeting.length">
          <div class="line"></div>
          <el-card v-for="item in notTodayMeeting" :key="item.id" class="h-300">
            <el-row style="text-align: center"
              ><strong :title="item.title">{{ item.title }}</strong></el-row
            >
            <el-row
              ><span class="meeting-time-row"
                ><i class="el-icon-date"></i>{{ item.time }}</span
              ><span class="meeting-place-row" :title="item.place"
                ><i class="el-icon-location"></i>{{ item.place }}</span
              ></el-row
            >
            <el-row style="margin: 5px 0 10px 0"
              >参会人员：<span :title="item.attendee">{{
                item.attendee
              }}</span></el-row
            >
            <div class="meeting-content">会议内容：{{ item.content }}</div>
          </el-card>
        </div>
        <el-empty
          description="暂无会议"
          v-if="!notTodayMeeting.length && !todayMeeting.length"
        >
        </el-empty>
      </el-tab-pane>
      <el-tab-pane>
        <span slot="label"><i class="el-icon-view"></i> 消息中心</span>

        <div class="msg-box" v-if="messageInfo.length">
          <el-card
            style="width: 45%; min-height: 60px"
            v-for="(item, index) in messageInfo"
            :key="index"
          >
            {{ item.content }}
          </el-card>
        </div>
        <el-empty description="暂无消息" v-else></el-empty>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { getAllmeeting } from '@/api/meeting'
export default {
  data () {
    return {
      todayMeeting: [],
      notTodayMeeting: [],
      // messageInfo: this.$store.state.user.messageInfo,
      messageInfo: localStorage.getItem('messageInfos')
        ? JSON.parse(localStorage.getItem('messageInfos'))
        : []
    }
  },
  methods: {
    // 获取所有会议记录
    async getMeeting () {
      const data = await getAllmeeting()
      const date = new Date()
      const year = date.getFullYear()
      const month = (date.getMonth() + 1).toString().padStart(2, '0')
      const day = date.getDate().toString().padStart(2, '0')
      const today = year + '/' + month + '/' + day

      for (const meeting of data) {
        // 今天的会议
        if (meeting.time.indexOf(today) !== -1) {
          this.todayMeeting.push(meeting)
        } else {
          this.notTodayMeeting.push(meeting)
        }
      }
    }
  },
  created () {
    this.getMeeting()
  }
}
</script>

